:root {
  --dark: #0f1c13;
  --dark-accent: #af5233;
  --light: #fcfaf9;
  --light-accent: #fcfaf9;
  --default-font-family: "Century Gothic", sans-serif;
  --serif-font-family: "Italiana", serif;
}

.theme {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  transition: background-color .6s ease-in-out, color .6s ease-in-out;
}

.theme.--dark {
  --primary-color: var(--dark);
  --secondary-color: var(--light);
  --accent-color: var(--dark-accent);
}

.theme.--light {
  --primary-color: var(--light);
  --secondary-color: var(--dark);
  --accent-color: var(--light-accent);
}

.theme ::selection {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.theme--border {
  border-color: var(--secondary-color);
  transition: border-color .6s ease-in-out;
}

.theme--background {
  background-color: var(--secondary-color);
  transition: background-color .6s ease-in-out;
}

.theme--color {
  color: var(--secondary-color);
  transition: color .6s ease-in-out;
}

.theme--fill path {
  fill: var(--secondary-color);
  transition: fill .6s ease-in-out;
}

*, :before, :after {
  box-sizing: border-box;
  text-rendering: optimizelegibility;
  -webkit-tap-highlight-color: transparent;
}

html {
